const actions = {
  getAssetTypes: (page, search, filter) => ({
    type: 'GET_ASSET_TYPES_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getAssetFiscal: id_asset_fiscal => ({
    type: 'GET_ASSET_FISCAL_REQUEST',
    payload: { id_asset_fiscal: id_asset_fiscal }
  }),
  createAssetType: () => ({
    type: 'CREATE_ASSET_TYPE_REQUEST'
  }),
  storeAssetType: data => ({
    type: 'STORE_ASSET_TYPE_REQUEST',
    payload: { body: data }
  }),
  editAssetType: id_asset_type => ({
    type: 'EDIT_ASSET_TYPE_REQUEST',
    payload: { id_asset_type: id_asset_type }
  }),
  updateAssetType: (id_asset_type, data) => ({
    type: 'UPDATE_ASSET_TYPE_REQUEST',
    payload: {
      id_asset_type: id_asset_type,
      body: data
    }
  }),
  showAssetType: id_asset_type => ({
    type: 'SHOW_ASSET_TYPE_REQUEST',
    payload: { id_asset_type: id_asset_type }
  }),
  deactivateAssetType: id_asset_type => ({
    type: 'DEACTIVATE_ASSET_TYPE_REQUEST',
    payload: { id_asset_type }
  }),
  activateAssetType: id_asset_type => ({
    type: 'ACTIVATE_ASSET_TYPE_REQUEST',
    payload: { id_asset_type }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
