import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import AssetTypeUpdateForm from "./updateForm";
import notification from "../../../../components/notification";
import { Spin } from "antd";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/assetType/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/asset-types";

class UpdateAssetType extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editAssetType } = this.props;
    const { id } = this.props.match.params;
    editAssetType(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.assetType.error) {
      notification("error", this.props.assetType.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.assetType.success) {
      this.props.history.replace(redirectTo);
    }
  }

  handleSubmit = value => {
    const { updateAssetType } = this.props;
    const { id } = this.props.match.params;
    updateAssetType(id, value);
  };

  render() {
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="assetTypes.update.title" />}
            style={{ margin: 0 }}
          >
            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.assetType.loadingEdit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
            <AssetTypeUpdateForm
              auth={this.props.auth}
              submit={this.handleSubmit}
              assetType={this.props.assetType}
            />
          )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    assetType: state.assetType
  };
};

export default compose(connect(mapStateToProps, actions))(UpdateAssetType);
